/*!
 * Global styles
 */
html,
body {
  color: #839496;
  background-color: #002b36;
  font-family: 'Open Sans', sans-serif;
  height: 100%;
}

html *, html:before, html:after,
html *:before,
html *:after,
body *,
body:before,
body:after,
body *:before,
body *:after {
  color: #839496;
  background-color: #002b36;
}

.glyphicon-spin {
  animation: glyphicon-spin 2s infinite linear;
}

@keyframes glyphicon-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}

/*!
 * Sidebar styles
 */
.sidebar-trigger {
  background-color: #586e75;
  border-radius: 50%;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  height: 50px;
  position: absolute;
  right: 10px;
  top: 10px;
  width: 50px;
  z-index: 2;
}

.sidebar-trigger__icon {
  /* this span is the central line of the menu icon */
  color: #657b83;
  background-color: #fdf6e3;
  display: inline-block;
  height: 3px;
  left: 13px;
  position: absolute;
  top: 23px;
  transition: background-color 0.3s;
  width: 24px;
  /* these are the upper and lower lines in the menu icon */
}

.sidebar-trigger__icon *, .sidebar-trigger__icon:before, .sidebar-trigger__icon:after,
.sidebar-trigger__icon *:before,
.sidebar-trigger__icon *:after {
  color: #657b83;
  background-color: #fdf6e3;
}

.sidebar-trigger__icon::before, .sidebar-trigger__icon:after {
  content: '';
  height: 100%;
  position: absolute;
  right: 0;
  transition: transform .3s, top .3s, background-color 0s;
  width: 100%;
}

.sidebar-trigger__icon::before {
  top: -6px;
}

.sidebar-trigger__icon::after {
  top: 6px;
}

.sidebar-trigger__icon--clicked {
  background-color: rgba(0, 43, 54, 0);
}

.sidebar-trigger__icon--clicked::before {
  top: 0;
  transform: rotate(135deg);
}

.sidebar-trigger__icon--clicked::after {
  top: 0;
  transform: rotate(225deg);
}

.sidebar {
  /* by default it's hidden - on top of the viewport */
  height: 100%;
  left: 0;
  overflow: hidden;
  position: fixed;
  top: 0;
  transform: translateY(-100%);
  transition-property: transform;
  transition-duration: 0.4s;
  width: 100%;
  z-index: 1;
}

.sidebar--visible {
  transform: translateY(0);
}

.sidebar__checkbox {
  display: none;
}

.sidebar__item {
  border-top: 1px solid #586e75;
  display: block;
  margin: 0;
  padding: 10px;
  transition: background-color 0.3s, color 0.3s;
}

.sidebar__item--selected {
  background-color: #eee8d5;
  border-top-color: #93a1a1;
  transition: background-color 0.3s, color 0.3s;
}

.sidebar__item--selected .sidebar__name,
.sidebar__item--selected .sidebar__location {
  background-color: #eee8d5;
  transition: background-color 0.3s, color 0.3s;
}

.sidebar__item--selected .sidebar__name {
  color: #cb4b16;
}

.sidebar__item--selected .sidebar__location {
  color: #586e75;
}

.sidebar__name {
  color: #2aa198;
  font-size: 1.2em;
  letter-spacing: 0.05em;
  transition: background-color 0.3s, color 0.3s;
}

.sidebar__location {
  font-size: 0.9em;
  font-style: italic;
  font-weight: 400;
  letter-spacing: 0.1em;
  transition: background-color 0.3s, color 0.3s;
}

.sidebar__search {
  padding: 10px;
}

.sidebar__search__icon {
  color: #657b83;
  background-color: #fdf6e3;
}

.sidebar__search__icon *, .sidebar__search__icon:before, .sidebar__search__icon:after,
.sidebar__search__icon *:before,
.sidebar__search__icon *:after {
  color: #657b83;
  background-color: #fdf6e3;
}

.sidebar__title {
  color: #d33682;
  text-align: center;
}

@media (min-width: 992px) {
  .sidebar {
    float: left;
    position: relative;
    transform: initial;
    width: 320px;
  }
  .sidebar-trigger {
    display: none;
  }
}

/*!
 * Map styles
 */
.map {
  color: initial;
  background-color: initial;
  background-color: gray;
  height: 100%;
  overflow: hidden;
}

.map *, .map:before, .map:after,
.map *:before,
.map *:after {
  color: initial;
  background-color: initial;
}

/*!
 * Info Window of Google Map
 */
.info-window__button {
  background-color: #002b36;
  color: #2aa198;
  font-size: 1.15em;
  font-weight: 700;
  margin: auto;
  padding: 5px;
  text-align: center;
  width: 80%;
}

.info-window__icon:before {
  color: #2aa198;
  font-size: 1.2em;
  margin-right: 5px;
  position: relative;
  top: 2px;
}

.info-window__location {
  color: #586e75;
  font-style: italic;
  letter-spacing: 0.03em;
  margin-bottom: 20px;
}

.info-window__name {
  border-bottom: 1px solid #586e75;
  color: #cb4b16;
  font-size: 1.4em;
  font-weight: 700;
  letter-spacing: 0.05em;
  padding-bottom: 10px;
}

/*!
 * Infos styles
 */
.modal-dialog {
  height: 96%;
  left: 2%;
  margin: 0;
  position: absolute;
  top: 2%;
  width: 96%;
}

.infos {
  color: #657b83;
  background-color: #fdf6e3;
  height: 100%;
  overflow: auto;
}

.infos *, .infos:before, .infos:after,
.infos *:before,
.infos *:after {
  color: #657b83;
  background-color: #fdf6e3;
}

.infos .modal-header {
  border-bottom-color: #586e75;
}

.infos .btn-api {
  margin-right: 5px;
}

.infos .spinner {
  font-size: 2em;
}

.infos__button {
  background-color: #002b36;
  color: #2aa198;
  font-weight: 700;
  letter-spacing: 0.05em;
}

.infos__close {
  font-size: 1em;
}

.infos__location {
  color: #586e75;
  font-style: italic;
  letter-spacing: 0.03em;
}

.infos__name {
  color: #cb4b16;
  font-size: 1.4em;
  font-weight: 700;
  letter-spacing: 0.05em;
  padding-bottom: 10px;
}

.foursquare__item {
  border-bottom: 1px solid #586e75;
  padding-bottom: 15px;
  margin-bottom: 25px;
}

.foursquare__rating {
  padding: 0 7px;
  color: #002b36;
  font-weight: 700;
}

.foursquare__title {
  border-bottom: 1px solid #586e75;
  padding: 10px;
  text-align: center;
  margin-bottom: 25px;
}

@media (min-width: 992px) {
  .foursquare__item {
    float: left;
    width: 50%;
  }
}

@media (min-width: 1200px) {
  .foursquare__item {
    width: 33.333%;
  }
}
